.home {
    background-color: #423900;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow:hidden;
    position: relative;
}
homeImage{
    height: 75%;
    background-image: url("../../components/images/me1.jpg");
    background-repeat: no-repeat;
    border-radius: 5%;
    background-size: contain;
}
.img1 {
    width: 100px;
    height: 100px;
    /* left: 120px; */
    position: absolute;
    z-index: 1;
    animation: 5s ease-in 1s infinite img1Floater;
}
